.card {
  max-width: 200px;
  padding: 32px 16px 16px;
  border-radius: 5px;
  border: 1px solid #f3f3f3;
  transition: transform $duration-effect;
  cursor: pointer;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    display: block;
    margin: auto;
    width: 160px;
    height: 134px;
    padding-bottom: 40px;
  }

  &__title {
    font-size: 12px;
    line-height: 18px;
    font-weight: 500;
    color: $main-text-color;
    padding-bottom: 4px;

    &:hover {
      color: #34568b;
    }
  }

  &__product-code {
    color: $secondary-text-color;
    padding-bottom: 16px;
    font-size: 10px;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 16px;

    &-text {
      font-size: 12px;
      color: $secondary-text-color;
      line-height: 18px;
    }

    &-sum {
      font-size: 16px;
      font-weight: 700;
      color: $main-text-color;
      line-height: 18px;
    }
  }

  &__button {
    display: flex;
    max-width: 164px;
    height: 38px;
    border-radius: 5px;
    background-color: $secondary-color;
    color: $main-color;
    text-align: center;
    justify-content: center;
    font-size: 14px;
    font-weight: 700;
    line-height: 38px;
    text-transform: uppercase;
    text-decoration: none;
    border: 1px solid $secondary-color;
    transition: background-color $duration-effect;

    &:hover {
      color: $secondary-color;
      background-color: $main-color;
    }
  }

  &__reviews {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 24px;

    &-text {
      font-size: 10px;
      color: $main-text-color;
    }
  }
}
